<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>列表展示所有文章信息</li>
      <li>通过文章分类或标题检索文章</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>文章分类</label>
        <el-select @change="query" v-model="queryParams.cateId">
          <el-option label="全部" value=""></el-option>
          <el-option v-for="item in artCateList" :key="item.id"
          :label="item.name" :value="item.id"></el-option>
        </el-select>
      
      </div>
      <div class="query-group">
        <label>文章标题</label>
        <el-input v-model="queryParams.title" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/admin/save'" icon="add" type="success" size="small" @click="$routerLink('/articleManager/save','save')">新增</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="title" label="标题" min-width='120'></el-table-column>
      <el-table-column prop="category.name" label="分类名称" min-width="160"></el-table-column>
      <el-table-column prop="show" label="是否显示" width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.show" :id='scope.row.id' @change="toggleVisable"></cms-switch>
        </div>
      </el-table-column>
       <el-table-column label="排序" prop="sortNum">
        <template slot-scope="scope">
          <el-input v-model="scope.row.sortNum"  @keyup.enter.native="sortNum($event,scope.row.id)"> </el-input>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="添加时间" min-width="100">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width='260' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click.stop="view(scope.row.id)">浏览</cms-button>
          <cms-button  v-prem="'/articleManager/edit'"   icon='edit' size='small' type='info' @click.stop="$routerLink('/articleManager/edit','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button     icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  artManagerList,
  artManagerShow,
  artManagerHide,
  artManagerSort,
  adminDelete,
  artCategory,
  artManagerGet
} from '@/api/article/articleManager.js'
var defaultData = {
  cateId: '',
  title: '',
  size: 10,
  page: 1
}
export default {
  // name: 'adminList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      artCateList: [],
      url: ''
    }
  },
  methods: {
    view(id) { // 前台浏览
      const base = process.env.baseUrl
      artManagerGet({ id: id }).then(res => {
        if (res.data.linkUrl === '') {
          this.cateId = res.data.cateId
          window.open(`${base}/helpCenter.htm?cateId=${this.cateId}&articleId=${id}`)
        } else {
          window.open(res.data.linkUrl)
        }
      })
    },
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(adminDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(adminDelete, this.ids) // 这个才是真实的删除方法
    },
    toggleVisable(status, id) {
      if (status) {
        artManagerShow({ ids: [id] }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      } else {
        artManagerHide({ ids: [id] }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      }
    },
    getArtCate() {
      // 获取文章分类
      artCategory({ parentId: '' }).then(res => {
        if (res.code === 200) {
          this.artCateList = res.data
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          artManagerSort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    }

  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(artManagerList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(artManagerList, this.queryParams)
    this.getArtCate()
  }
}
</script>

<style>
</style>



